// import aboutUs from "../pages/myDatas/aboutUs.vue";

import smallClass from "../pages/course/subPages/smallClass.vue";

import csAnswer from "../pages/course/withClass/csAnswer.vue";

Vue.use(Router);

const routes = [
  {
    path: "/",
    redirect: "/login"
  },
  {
    path: "/login",
    meta: { title: "", from: "", limits: [LimitType.top] },
    component: () => import("@/pages/user/login.vue")
  },
  {
    path: "/agreement",
    meta: { title: "", from: "" },
    component: () => import("@/pages/myDatas/agreement.vue")
  },
  {
    path: "/policy",
    meta: { title: "", from: "" },
    component: () => import("@/pages/myDatas/policy.vue")
  },
  {
    path: "/index",
    name: "index",
    component: IndexPage,
    redirect: "/homePage",
    children: [
      {
        path: "/homePage",
        name: "homePage",
        meta: { title: "", from: "", cache: true, limits: [LimitType.top] },
        component: () => import("@/pages/home/home-page.vue")
      },
      {
        path: "/homeCourse",
        name: "homeCourse",
        meta: { title: "", from: "", cache: true, limits: [LimitType.top] },
        component: () => import("@/pages/home/home-course.vue")
      },
      {
        path: "/myZone",
        name: "myZone",
        meta: {
          title: "",
          from: "",
          cache: true,
          limits: [LimitType.top]
        },
        component: () => import("@/pages/home/my-zone.vue")
      }
    ]
  },
  //
  {
    path: "/personal",
    // name: 'personalDatas',
    component: IndexPage,
    redirect: "/personalDatas",
    children: [
      {
        path: "/personalDatas",
        // name: 'myStats',
        meta: { title: "", from: "/myZone" },
        component: () => import("@/pages/myDatas/personalDatas.vue")
      },
      {
        path: "/helpUse",
        // name: 'myStats',
        meta: { title: "", from: "/myZone" },
        component: () => import("@/pages/myDatas/helpUse.vue")
      },
      {
        path: "/myStats",
        // name: 'myStats',
        meta: { title: "", from: "/myZone" },
        component: () => import("@/pages/myDatas/myStats.vue")
      },
      {
        path: "/aboutUs",
        // name: 'aboutUs',
        meta: { title: "", cache: true, from: "/myZone" },
        component: () => import("@/pages/myDatas/aboutUs.vue")
      }
    ]
  },
  {
    path: "/smallClasssmallClass",
    name: "smallClass",
    meta: { title: "", from: "/course" },
    component: smallClass
  },
  {
    path: "/csAnswer",
    name: "csAnswer",
    component: csAnswer
  },
  { path: "*", redirect: "/" } //
];

const courseRouter = [
  {
    path: "/course",
    name: "course",
    meta: {
      title: "",
      from: "/homeCourse",
      cache: true,
      limits: [LimitType.inCourse]
    },
    component: () => import("@/pages/course/course.vue")
  },
  {
    path: "/signIn/:way/:state?",
    name: "signIn",
    meta: { title: "", from: "/course", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/sign/signIn.vue")
  },
  {
    path: "/blackboardWriting",
    name: "boardNote",
    meta: { title: "", from: "/course", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/boardNote/blackboardWriting.vue")
  },
  {
    path: "/groupNote",
    name: "groupNote",
    meta: { title: "", from: "/course", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/groupNote/index.vue")
  },
  {
    path: "/quiz/:selected?",
    name: "quiz",
    meta: {
      title: "",
      from: "/course",
      cache: true,
      limits: [LimitType.inCourse]
    },
    component: () => import("@/pages/course/quiz/index.vue")
  },
  {
    path: "/screenQuestion/:id?",
    name: "screenQuestion",
    meta: {
      title: "",
      from: "/quiz",
      limits: [LimitType.inCourse],
      unAutoNote: true
    },
    component: () => import("@/pages/course/quiz/screenQuestion.vue")
  },
  {
    path: "/classPractice/:id?/:bankId?/:total?/:state?",
    name: "classPractice",
    meta: {
      title: "",
      from: "/quiz",
      limits: [LimitType.inCourse],
      unAutoNote: true
    },
    component: () => import("@/pages/course/quiz/classPractice.vue")
  },
  {
    path: "/note",
    name: "note",
    meta: {
      title: "",
      from: "/course",
      cache: true,
      limits: [LimitType.inCourse]
    },
    component: () => import("@/pages/course/note/note.vue")
  },
  {
    path: "/noteDetail",
    name: "noteDetail",
    meta: { title: "", from: "/note", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/note/noteDetail.vue")
  },
  {
    path: "/editNote",
    name: "editNote",
    meta: {
      title: "",
      from: "/note",
      limits: [LimitType.inCourse],
      unAutoNote: true
    },
    component: () => import("@/pages/course/note/editNote.vue")
  },
  {
    path: "/question",
    name: "question",
    meta: { title: "", from: "/course", limits: [LimitType.inCourse] },
    component: () => import("@/pages/course/question/question.vue")
  },
  {
    path: "/courseware",
    name: "courseware",
    meta: { title: "", from: "", limits: [LimitType.inCourse, LimitType.afterCourse] },
    component: () => import("@/pages/course/courseware/index.vue")
  },
  {
    path: "/fileView/:id/:fileExt/:name",
    name: "fileView",
    meta: { title: "", from: "" },
    component: () => import("@/pages/course/courseware/fileView.vue")
  },
  {
    path: "/coursewareView/:id/:fileExt/:name",
    name: "coursewareView",
    meta: {
      title: "",
      from: "/courseware",
      limits: [LimitType.inCourse, LimitType.afterCourse]
    },
    component: () => import("@/pages/course/courseware/fileView.vue")
  },
  {
    path: "/liveLesson",
    name: "liveLesson",
    meta: {
      title: "",
      from: "/course",
      limits: [LimitType.inCourse]
    },
    component: () => import("@/pages/course/liveLesson/index.vue")
  },
  {
    path: "/liveLessonType",
    name: "liveLessonType",
    meta: {
      title: "",
      from: "",
      limits: [LimitType.inCourse, LimitType.afterCourse]
    },
    component: () => import("@/pages/course/liveLesson/typeList.vue")
  },
  {
    path: "/livePlayback/:type/:index",
    name: "livePlayback/:type/:index",
    meta: {
      title: "",
      from: "",
      limits: [LimitType.inCourse, LimitType.afterCourse]
    },
    component: () => import("@/pages/course/liveLesson/playback.vue")
  },

  {
    path: "/groupDiscussion",
    name: "groupDiscussion",
    meta: {
      title: "",
      from: "/course",
      limits: [LimitType.inCourse, LimitType.group],
      unAutoNote: true
    },
    component: () => import("@/pages/course/group/groupDiscussion.vue")
  },
  {
    path: "/teamRank",
    name: "teamRank",
    meta: {
      title: "",
      from: "",
      limits: [LimitType.inCourse, LimitType.group]
    },
    component: () => import("@/pages/course/group/teamRank.vue")
  },
  {
    path: "/joinGroup",
    name: "joinGroup",
    meta: {
      title: "",
      from: "/course",
      limits: [LimitType.inCourse]
    },
    component: () => import("@/pages/course/group/joinGroup.vue")
  },
  {
    path: "/groupInfo/:groupId?",
    name: "groupInfo",
    meta: {
      title: "",
      from: "/joinGroup",
      limits: [LimitType.inCourse]
    },
    component: () => import("@/pages/course/group/groupInfo.vue")
  }
];

